@import "../_global-editor-style/scss/variables";



// fix WordPress vs. Bootstrap conflicts (overwrite `.editor-styles-wrapper .wp-block` default styles)

.editor-styles-wrapper [data-type="bsx-blocks/row-with-cols"].row.wp-block:not(.no-gutters) {
	margin-left: calc($bsx-grid-gutter-width / -2);
	margin-right: calc($bsx-grid-gutter-width / -2);
}

